













































import Vue from "vue";
import moment from "moment";

export default Vue.extend({
  props: [
    "time",
    "temp",
    "humidity",
    "windDirection",
    "windSpeed",
    "hidden",
    "precipitation",
    "summary",
  ],
  data: () => ({}),
  computed: {
    timeFormated() {
      return moment(this.time).format("LT");
    },
  },
  methods: {
    weatherIconPath(weather: string) {
      return `${process.env.BASE_URL}weathericon/svg/${weather}.svg`;
    },
    asRotTransform(angle: number) {
      return `rotate(${angle})`;
    },
  },
});
